<template>
  <div>
    <div>
    <van-nav-bar
      title="会员登录"
      left-arrow
      @click-left="$router.go(-1)"

    />
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="请输入手机号码" type="text">
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="5" placeholder="请输入图形验证码" type="text">
          <img v-if="picUrl" :src="picUrl" @click="getPicCode" alt="">
        </div>
        <div class="form-item">
          <input v-model="msgCode" class="inp" placeholder="请输入短信验证码" type="text">
          <button @click="getCode">{{second===totalSecond?'获取验证码':second+'秒后获取'}}</button>
        </div>
      </div>

      <div @click="login" class="login-btn">登录</div>
    </div>
  </div>

  </div>
</template>

<script>
import { getPicCode, getMsgCode, codeLogin } from '@/api/login'

// import { Toast } from 'vant'
export default {
  name: 'LoginIndex',
  data () {
    return {
      mobile: '', // 当前的手机号
      picCode: '', // 用户输入的图形验证码
      picUrl: '', // 图片地址
      picKey: '', // 请求回来的图形验证码唯一标识
      totalSecond: 60, // 总秒数
      second: 60, // 当前秒数，开定时器对secoond
      timer: null,
      msgCode: ''// 短信验证码
    }
  },

  async created () {
    this.getPicCode()
  },
  destroyed () {
    clearInterval(this.timer)
  },
  methods: {
    // 验证手机号和图像验证码的格式是否正确
    vaildFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('请输入正确的图形验证码')
        return false
      }
      return true
    },
    // 请求图形验证码
    async getPicCode () {
      const { data: { base64, key } } = await getPicCode()
      // console.log(res)
      this.picUrl = base64
      this.picKey = key
      // Toast('siusiusiu')
      // 不需要组件内导入，直接挂在原型上
      // this.$toast('siusiusiu')
      this.$toast.success('siu')
    },

    // 设置发送短信验证码的时间间隔
    async getCode () {
      if (!this.vaildFn()) {
        return
      }
      if (!this.timer && this.second === this.totalSecond) {
        // 发送请求
        // const res = await getMsgCode(this.picCode, this.picKey, this.mobile)
        // console.log(res)
        // 预期，如果响应的status非200，最好抛出一个promise错误，await只会等待成功的promise
        await getMsgCode(this.picCode, this.picKey, this.mobile)
        // this.$toast('短信发送成功')

        this.timer = setInterval(() => {
          this.second--
          console.log('倒计时ing')
          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.second = this.totalSecond
          }
        }, 1000)
        console.log(this.timer)
      }
    },
    async login () {
      if (!this.vaildFn()) {
        return
      }
      if (!/^\d{6}$/.test(this.msgCode)) {
        this.$toast('gun输入正确的短信验证码')
        return
      }
      const res = await codeLogin(this.mobile, this.msgCode)
      this.$store.commit('user/setUserInfo', res.data)
      this.$toast('登录成功')
      // 进行判定，看地址栏有没跳转地址
      // 1，如果有=》需要回跳
      // 2，如果没有=》正常去首页
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
      // this.$toast('login,bt')
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
